import {lineh, measureMarginLeft, xPerQuarter} from '../config/MusicConfig';

export const BeamBegin = "begin"
export const BeamEnd = "end"
export const BeamContinue = "continue"
export const BeamBackward = "backward hook"
export const BeamForward = "forward hook"

class MusicMeasure {
	constructor() {
		this.staves = [[]]
	}
	/*
	2，4连音替2个
	3连音替2个
	5，6，7连音替4个
	9-15连音替8个
	17-31连音替16个
	*/
	measureTicks(beats, beatUnit) {
		// console.log('notes0: '+measure.notes.length);
		// var tupletCount = 0;
		// var tupletStart = -1;
		var maxTicks = 0

		for (var staffIndex = 0; staffIndex < this.staves.length; staffIndex++) {
			var ticks = 0;
			var notes = this.staves[staffIndex]
			for (var i = 0; i < notes.length; i++) {
				var n = notes[i];
				if (n.note) {
					// if (n.tuplet) {
					// 	tupletCount = n.tuplet.count;
					// 	tupletStart = i;
					// }
					var t = this.noteDuration(n);
					// console.log("i:"+i+" tupletStart:"+tupletStart+" t:"+t)
					// if (tupletStart>=0 && i<=tupletStart+tupletCount) {
					// 	if (tupletCount<5) {
					// 		t = t*2/tupletCount
					// 	}else if (tupletCount<8) {
					// 		t = t*4/tupletCount
					// 	}else if (tupletCount<16) {
					// 		t = t*8/tupletCount
					// 	}else if (tupletCount<32) {
					// 		t = t*16/tupletCount
					// 	}
					// }
					// console.log("i:"+i+" tupletCount:"+tupletCount+" t:"+t)
					ticks += t;
				}
				if (ticks>beats*480*4/beatUnit) {
					notes.splice(i, notes.length-i)
					break
				}
			}
			if (ticks>maxTicks) {
				maxTicks = ticks
			}
		}
		return maxTicks
	}
	delNote(notePos) {
		var notes = this.staves[notePos.staffIndex]
		notes.splice(notePos.noteIndex, 1)
		if (notes.length===0) {
			notes.push({note:1, rest: true})
		}
	}
	isEmpty() {
		for (var i = 0; i < this.staves.length; i++) {
			var notes = this.staves[i]
			if (notes.length>1) {
				return false
			}
			if (notes.length === 1 && notes[0].note !== 1) {
				return false
			}
		}
		return true
	}
	noteDuration(note) {
		if (!note.note) {
			return 0
		}
		var duration = 480*4/note.note;
		if (note.dots) {
			duration *= 1.5;
		}
		return duration;
	}
	removeBeams(staffIndex, noteIndex) {
		var note = null
		var notes = this.staves[staffIndex]
		for (var i = noteIndex; i >= 0; i--) {
			note = notes[i]
			if (note.beams && note.beams[0].type===BeamBegin) {
				break
			}
		}
		if (note && note.beams) {
			const beamNotes = note.beams[0].beamNotes
			if (beamNotes) {
				for (var n=0; n<beamNotes.length; n++) {
					const midNote = notes[beamNotes[n]]
					if (midNote) {
						midNote.beams = null
					}
				}
			}
			note.beams = null
		}
	}
	findBeamNotes(notes, from, beamIndex=0) {
		var beamNotes = [from]
		for (var next = from+1; next < notes.length; next++) {
			var nextNote = notes[next]
			if (nextNote.rest || !nextNote.beams) {
				continue
			}
			beamNotes.push(next)
			// beamNotes.splice(beamNotes, 0, next)
			if (nextNote.beams && beamIndex<nextNote.beams.length && nextNote.beams[beamIndex].type === BeamEnd) {
				return beamNotes.reverse()
			}
		}
		if (beamNotes.length>1) {
			notes[beamNotes[0]].beams[beamIndex].type = BeamEnd
		}
		return beamNotes.reverse()
	}
	noteTopBottomLine(note) {
		// if ((note.topLine || note.bottomLine) && note.elems.length>1) {
		// 	return {topLine: note.topLine, bottomLine: note.bottomLine}
		// }
		var topLine=-100
		var bottomLine=100
		if (note.elems) {
			for (var i = 0; i < note.elems.length; i++) {
				const elem = note.elems[i]
				const line = elem.line ? elem.line : 0
				if (line>topLine) {
					topLine = line
				}
				if (line<bottomLine) {
					bottomLine = line
				}
			}
		}else{
			topLine = 0
			bottomLine = 0
		}
		note.topLine=topLine
		note.bottomLine=bottomLine
		return {topLine: note.topLine, bottomLine: note.bottomLine}
	}
	setBeamEndx(note, notes, n) {
		for (let i = 0; i < note.beams.length; i++) {
			let beam = note.beams[i]
			if (beam.type !== BeamBegin) {
				continue
			}
			var beamNotes = beam.beamNotes
			if (!beamNotes) {
				beamNotes=this.findBeamNotes(notes, n, i)
				// console.log(beamNotes)
				if (beamNotes) {
					beam.beamNotes = beamNotes
					beam.endNoteIndex = beamNotes[0]
				}else{
					console.log(n)
					console.log(notes)
				}
			}
			// console.log("calcElemLines:"+n)
			// console.log(note.beams)
			let endnote = notes[beamNotes[0]]
			if (!endnote) {
				//如果beam后面的note被删除了
				note.beams=null
				for(let beamNoteIndex of beamNotes) {
					let temp = notes[beamNoteIndex]
					if (temp) {
						temp.beams = null
					}
				}
				return
			}

			if (beamNotes && i===0) {
				this.setBeamStemLine(notes, beamNotes)
				for (let k = 0; k < note.beams.length; k++) {
					let beam = note.beams[k]
					beam.endx = endnote.x-note.x
		        	beam.endline = endnote.stemLine
				}
			}else if (beamNotes) {
				beam.endx = endnote.x-note.x
				beam.endline = endnote.stemLine
			}
		}
	}
	calcElemLines(beatUnit) {

		for (var staffIndex = 0; staffIndex < this.staves.length; staffIndex++) {
			var notes = this.staves[staffIndex]
			for (var n = 0; n < notes.length; n++) {
				const note = notes[n]
				var topBottomLine = this.noteTopBottomLine(note)
				if (note.beams) {
					this.setBeamEndx(note, notes, n)
					continue
				}
				if (topBottomLine.topLine+topBottomLine.bottomLine>0) {
					note.stemUp = false
					note.stemLine = topBottomLine.bottomLine-6
					if (note.stemLine>1) {
						note.stemLine = 1
					}
				}else{
					note.stemUp = true
					note.stemLine = topBottomLine.topLine+6
					if (note.stemLine<-1) {
						note.stemLine=-1
					}			
				}
			}
		}
	}

	changeBeam(notePos) {
		var notes = this.staves[notePos.staffIndex]
		var noteIndex = notePos.noteIndex
		const note = notes[noteIndex]
		if (note.rest || note.note<8 || noteIndex === 0) {
			return
		}
		var prevNote = notes[noteIndex-1]
		if (prevNote.rest && noteIndex>1) {
			prevNote = notes[noteIndex-2]
			if (prevNote.rest) {
				return
			}
		}
		if (prevNote.note<8 && !prevNote.rest) {
			return
		}
		if (note.beams) {
			console.log(note.beams)
			if (note.beams[0].type === BeamEnd) {
				note.beams = null
				if (prevNote.beams) {
					if (prevNote.beams[0].type === BeamBegin) {
						prevNote.beams = null
					}else{
						prevNote.beams[0].type = BeamEnd
						this.setBeamEndNoteIndex(notes, noteIndex-1)
					}
				}else{
					console.log("Error, prevNote should not be empty")
				}
				return
			}else if (note.beams[0].type === BeamContinue) {
				return
			}
		}
		if (!note.beams && prevNote.beams) {
			prevNote.beams[0].type = BeamContinue
			note.beams = [{num:0, type: BeamEnd}]
			this.setBeamEndNoteIndex(notes, noteIndex)
			return
		}
		if (!prevNote.beams){
			var endNoteIndex = noteIndex
			if (note.beams) {
				if (note.beams[0].type === BeamBegin) {
					note.beams[0].type = BeamContinue
					endNoteIndex = note.beams[0].endNoteIndex
				}else{
					console.log("Error, should be BeamBegin")
					console.log(note.beams)
				}
			}else{
				note.beams = [{num:0, type: BeamEnd}]
			}

			prevNote.beams = [{num:0, type: BeamBegin, endNoteIndex: noteIndex}]
			this.setBeamEndNoteIndex(notes, endNoteIndex)
		}
	}
	tailsOfNote(note) {
		if (note.note===8) {
			return 1
		}else if (note.note===16) {
			return 2
		}else if (note.note===32) {
			return 3
		}else if (note.note===64) {
			return 4
		}else if (note.note===128) {
			return 5
		}
		return 0
	}
	findBeamEndNodeIndex(notes, num, from, endNoteIndex) {
		var curEndNoteIndex = endNoteIndex
		for (var next = from+1; next <= endNoteIndex; next++) {
			const note = notes[next]
			if (note.rest) {
				continue
			}
			if (num>=note.beams.length) {
				curEndNoteIndex = next-1
				break
			}
		}
		return curEndNoteIndex
	}
	setBeamEndNoteIndex(notes, endNoteIndex) {
		var beamNotes = []
		var nextTails = 0
		for (var prev = endNoteIndex; prev >= 0; prev--) {
			const midNote = notes[prev]
			if (midNote.rest || !midNote.beams) {
				continue
			}

			beamNotes.push(prev)			
			var foundBeamBegin = false
			if (midNote.beams[0].type === BeamBegin) {
				foundBeamBegin = true
			}
			var prevTails = 0
			if (!foundBeamBegin && prev>0) {
				var prevNote = notes[prev-1]
				if (prevNote.rest) {
					prevNote = notes[prev-2]
				}
				prevTails = this.tailsOfNote(prevNote)
			}
			const tails = this.tailsOfNote(midNote)
			// console.log("tails:"+prev+":"+prevTails+" "+tails)
			midNote.beams = []
			for (var i = 0; i < tails; i++) {
				if (prev===endNoteIndex) {
					if (i>=prevTails) {
						midNote.beams.push({num:i, type: BeamBackward})
					}else{
						midNote.beams.push({num:i, type: BeamEnd})
					}
				}else if (foundBeamBegin) {
					if (i>=nextTails) {
						midNote.beams.push({num:i, type: BeamForward})
					}else{
						const curEndNoteIndex = this.findBeamEndNodeIndex(notes, i, prev, endNoteIndex)
						midNote.beams.push({num:i, type: BeamBegin, endNoteIndex: curEndNoteIndex, beamNotes: beamNotes})
					}
				}else if (i>=prevTails) {
					if (i>=nextTails) {
						if (nextTails>prevTails) {
							midNote.beams.push({num:i, type: BeamForward})
						}else{
							midNote.beams.push({num:i, type: BeamBackward})
						}
					}else{
						const curEndNoteIndex = this.findBeamEndNodeIndex(notes, i, prev, endNoteIndex)
						midNote.beams.push({num:i, type: BeamBegin, endNoteIndex: curEndNoteIndex, beamNotes: beamNotes})
					}
				}else{
					midNote.beams.push({num:i, type: BeamContinue})
				}
			}
			// console.log("midNote:"+prev)
			// console.log(midNote.beams)

			nextTails = tails
			if (foundBeamBegin) {
				this.setBeamStemLine(notes, beamNotes)
				break
			}
		}
	}
	setBeamStemLine(notes, beamNotes) {
		// console.log("setBeamStemLine")
		// console.log(beamNotes)
		const stopNote = notes[beamNotes[0]]
		const startNote = notes[beamNotes[beamNotes.length-1]]
		if (!stopNote) {
			return
		}
		var topBottomLine = this.noteTopBottomLine(stopNote)
		var topLine = topBottomLine.topLine
		var bottomLine = topBottomLine.bottomLine

		var beamDuration = 0
		for (var beamIndex=1; beamIndex<beamNotes.length; beamIndex++) {
			const midNote = notes[beamNotes[beamIndex]]
			beamDuration += this.noteDuration(midNote)
			if (topLine<midNote.topLine) {
				topLine = midNote.topLine
			}
			if (bottomLine>midNote.bottomLine) {
				bottomLine = midNote.bottomLine
			}
		}
		var stemUp = true
		if (topLine+bottomLine>0) {
			stemUp = false
			if (!startNote.stemLine || startNote.stemLine>bottomLine-5) {
				startNote.stemLine = bottomLine-5
			}
			if (!stopNote.stemLine || stopNote.stemLine>bottomLine-5) {
				stopNote.stemLine = bottomLine-5
			}
		}else{
			if (!startNote.stemLine || startNote.stemLine<topLine+6) {
				startNote.stemLine = topLine+6
			}
			if (!stopNote.stemLine || stopNote.stemLine<topLine+6) {
				stopNote.stemLine = topLine+6
			}
		}
		var k = (stopNote.stemLine-startNote.stemLine)/beamDuration
		var midNoteTick = 0
		for (var n=beamNotes.length-1; n>=0; n--) {
			const midNote = notes[beamNotes[n]]
			midNote.stemUp = stemUp
			if (n>0 && n<beamNotes.length-1) {
				midNote.stemLine = k*midNoteTick+startNote.stemLine
			}
			midNoteTick += this.noteDuration(midNote)
		}
	}

	autoStemBeam(beatUnit){
		var tick = 0;
		var i = 0;
		const unitTicks = beatUnit===8 ? 240*3 : beatUnit===2 ? 480*2 : 480
		while (i<this.notes.length-1) {
			const note = this.notes[i]
			if (tick%unitTicks === 0 && note.note>4) {
				var quarter = this.noteDuration(note);
				var beamNotes = [i]
				// console.log("quarter:"+quarter)
				for (var next = i+1; next < this.notes.length; next++) {
					const nextNote = this.notes[next];
					if (nextNote.note === note.note) {
						const curNoteTicks = this.noteDuration(nextNote);
						quarter += curNoteTicks;
						// console.log("quarter:"+quarter+" unitTicks:"+unitTicks)
						if (quarter>unitTicks) {
							break
						}else {
							beamNotes.push(next)
							if (quarter === unitTicks) {
								note.beam = note.note===32 ? 3 : note.note===16 ? 2 : 1
								note.beamDuration = unitTicks-curNoteTicks
								note.beamNotes = beamNotes

								tick += quarter
								this.setBeamStemLine(beamNotes, note, nextNote)
								var k = (nextNote.stemLine-note.stemLine)/note.beamDuration
								var midNoteTick = this.noteDuration(note)
								for (var n=1; n<beamNotes.length; n++) {
									const midNote = this.notes[beamNotes[n]]
									midNote.inBeam = true
									midNote.stemUp = note.stemUp
									midNote.stemLine = k*midNoteTick+note.stemLine
									//midNote.beam = midNote.note===32 ? 3 : midNote.note===16 ? 2 : 1
									midNoteTick += this.noteDuration(midNote)
								}
								note.beamEndLine=nextNote.stemLine

								// console.log("stemLine1:"+note.stemLine+" top:"+note.topLine+" bottom:"+note.bottomLine+" line:"+note.line)
								// console.log("stemLine2:"+nextNote.stemLine+" top:"+nextNote.topLine+" bottom:"+nextNote.bottomLine+" line:"+nextNote.line)
								//i = next-1
								break
							}
						}
					}else{
						break
					}
				}
			}
			if (note.note) {
				tick += this.noteDuration(note);
			}
			i += 1
		}
	}
	rearrangeMeasure(beats, beatUnit) {
		var ticks = 0;
		// console.log('notes0: '+measure.notes.length);
		for (var i = 0; i < this.notes.length; i++) {
			var n = this.notes[i];
			if (n.note) {
				ticks += this.noteDuration(n);
			}
			if (ticks>beats*480*4/beatUnit) {
				this.notes.splice(i, this.notes.length-i)
				break
			}
		}
	}
	setTuplet(note, notes, noteIndex) {
		note.tuplet.beginline = note.stemUp ? note.stemLine-1 : note.topLine
		var stopNoteIndex = notes.length-1
		if (noteIndex<notes.length-note.tuplet.count+1) {
			stopNoteIndex = noteIndex+note.tuplet.count-1
		}
		const stopNote = notes[stopNoteIndex]
		note.tuplet.endline = stopNote.stemUp ? stopNote.stemLine-1 : stopNote.topLine
		note.tuplet.endx = stopNoteIndex>noteIndex ? stopNote.x-note.x : lineh*4

		note.tupletType = "start"
		stopNote.tupletType = "stop"
	}
	calcNoteOffsetx() {
		var max_x = 0
		for (var staffIndex = 0; staffIndex < this.staves.length; staffIndex++) {
			var x = measureMarginLeft
			var notes = this.staves[staffIndex]
			for (var i = 0; i < notes.length; i++) {
		        var note = notes[i];

		        note.x = x
		        if (note.type === "clef") {
					if (i===0) {
						note.x = -10*measureMarginLeft
					}else{
						note.x = x-lineh*2
					}
				}else if (note.note<2) {
					x += xPerQuarter
				}else if (note.note>0 && note.note<=16) {
					x += xPerQuarter*this.noteDuration(note)/480;
				}else if (note.ticks !== 0) {
					x += xPerQuarter*0.35;
				}
		    }
			if (x>max_x) {
				max_x=x
			}
		}
	    this.width = max_x+measureMarginLeft
	}
	resetTieType() {
		for (var staffIndex = 0; staffIndex < this.staves.length; staffIndex++) {
			var notes = this.staves[staffIndex]
			for (var i = 0; i < notes.length; i++) {
		        var note = notes[i];
		        note.tupletType = null;
		        if (note.elems) {
					for (var e = 0; e < note.elems.length; e++) {
						var elem = note.elems[e];
						elem.tieType = null
					}
		        }
		    }
		}
	}
}

export default MusicMeasure;



