import React, { Component } from 'react';
import Toolbar from "../components/Toolbar";
import MusicSetting from './MusicSetting';
import MusicEditor from './MusicEditor';
import MusicExam from './MusicExam';
import '../App.css';
import '../Home.css';

const homeIcons = {
	note: "\ue6c6",
	interval: "\ue6c4",
	chord: "\ue6c2",
	keysignature: "\ue6c3",
	scale: "\ue6c5",
}

class Home extends Component {
	constructor(props) {
		super(props)
		// window.level = 1
		this.exercises = [
			{
				title: "识别练习 Identification",
				type: "Identification",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['keysignature',"调号 Key Signature"],
					['scale',"音阶 Scale"],
					['terms',"音乐术语"],
				]
			},
			{
				title: "写作练习 Construction",
				type: "Construction",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					// ['keysignature',"调号 Key Signature"],
					['scale',"音阶 Scale"],
				]
			},
			{
				title: "弹奏练习 Keyboard",
				type: "Keyboard",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['scale',"音阶 Scale"],
					['rhythm',"节奏 Rhythm"],
				]
			},
			{
				title: "听辩练习 Ear Training",
				type: "EarTraining",
				items: [
					['note',"单音 Note"],
					['interval',"音程 Interval"],
					['chord',"和弦 Chord"],
					['scale',"音阶 Scale"],
					['rhythm',"节奏 Rhythm"],
				]
			},
			{
				title: "其他工具",
				type: "toolbox",
				items: [
					// ['exam',"模拟测试"],
					['editor',"创建曲谱"],
				]
			},
		]
		console.log("start home")
		console.log(window.stateHome)
		this.state = window.stateHome ? window.stateHome : {
			row: 0,
			column: 0,
			// level: window.level,
		}
	}
	setState(state) {
		super.setState(state)
		window.stateHome = state(this.state)
	}
	// setLevel(i) {
	// 	window.level=i
	// 	this.setState({level: i})
	// }
	takeAction(action) {
		let newState
		switch (action.type) {
		case 'moveColumn':
			let column = this.state.column + action.offset
			let row = this.state.row
			if (column<0) {
				if (row<=0) {
					return
				}else{
					row -= 1
					let exercise = this.exercises[row]
					column = exercise.items.length-1
				}
			}else{
				let exercise = this.exercises[row]
				if (column>=exercise.items.length) {
					if (row>=this.exercises.length-1) {
						return
					}else {
						row += 1
						column = 0
					}
				}
			}
			newState = {row: row, column: column}
			break
		case 'moveRow':
		{
			let row = this.state.row + action.offset
			if (row<0 || row>=this.exercises.length) {
				return
			}
			let column = this.state.column
			let exercise = this.exercises[row]
			if (column>=exercise.items.length) {
				column = exercise.items.length-1
			}
			newState = {row: row, column: column}
		}
			break
		case 'OK':
		{
			let exercise = this.exercises[this.state.row]
			let item = exercise.items[this.state.column]
			// window.location.href = this.linkOfExcise(exercise, item)
			window.theApp.pushWindow(this.linkOfExcise(exercise, item))
			return
		}
		default:
			break
		}
		this.setState(prevState => (newState))
	}
	linkOfExcise(exercise, item) {
		if (exercise.type === "toolbox") {
			if (item[0] === "editor") {
				return <MusicEditor />
			}else{
				return <MusicExam />
			}
		}else{
			return <MusicSetting type={item[0]} qtype={exercise.type}/>
		}
	}
	render() {
		let cellWidth = window.device==="piano" ? 320 : 170
		let elems = []
		for (var i = 0; i < this.exercises.length; i++) {
			let exercise = this.exercises[i]
			let values = exercise.items;
			let items = values.map((item, index) => {
				let className = "Home-icon"
				if(window.device !== "mobile" && this.state.row===i && this.state.column===index) {
					className += " Home-icon-hi"
				}
				let link = this.linkOfExcise(exercise, item)
				return(
						<div key={item[0]} onClick={(e) => {window.theApp.pushWindow(link)}} className={className} style={{width: cellWidth}}>
							<div className="iconfont" style={{display: 'table-cell', width: 32}}>{homeIcons[item[0]]}</div>
							<p style={{display: 'table-cell', verticalAlign: 'middle'}}>{item[1]}</p>
						</div>
					)
			})
			elems.push(
				<div key={i} style={{display: 'table'}}>
					<h3>{exercise.title}</h3>
					{items}
				</div>
				)
		}
		let toolbar = null
		if (window.device!=="mobile") {
			toolbar = <Toolbar systemButtons={{arrow: true, ok: "OK"}} action={(action) => this.takeAction(action)}/>
		}
		let leftDiv = null
		if (window.device === "piano") {
			const width =  window.document.body.offsetWidth
			leftDiv = <div style={{display: 'table-cell', width: width/2+10}}>
				v{window.ver}<p style={{fontSize:100, textAlign: 'center'}}>Find 音乐学院基本乐科</p>
			</div>
		}
		
		return (
			<div style={{padding: 10, display: 'table-cell'}}>
				{leftDiv}
				<div style={{display: 'table-cell'}}>{elems}</div>
				{toolbar}
			</div>)
	}
}

export default Home;



