
export var lineh=18;
export var xPerQuarter
export var measureMarginLeft
export var systemDistance, staffDistance
export var systemMarginLeft
export function zoom(offset) {
	if (offset>0) {
		lineh += 5
		if (lineh>94) {
			lineh = 94
		}
	}else if(offset<0) {
		lineh -= 5
		if (lineh<9) {
			lineh = 9
		}
	}
    xPerQuarter = lineh*6
    measureMarginLeft = lineh*1.0
    systemDistance = 9*lineh
    staffDistance = 7*lineh
    systemMarginLeft = 1*lineh
}
if (/(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)) {
	window.device = "mobile"
}else if (/(Safari|Chrome)/i.test(navigator.userAgent)) {
	window.device = "pc"
}else{
	window.device = 'piano'
	document.body.style.overflow="hidden";//隐藏页面水平和垂直滚动条  
}
if (window.device === "piano") {
	lineh = 32
}
window.ver = '0.9.1'
zoom(0)



