const terms = {
	"力度记号": dynamics,
	"力度变化记号": dynamicChange,
	"速度记号": tempoMarks,
	"变速记号": tempoChange,
	"反复记号": loopMarks,
}


class QMusicTerm extends QScore {
	constructor() {
		super()
		this.english = ""
		this.chinese = ""
		this.choiceNames = []
		this.choiceIndex = 0
		this.maxChoiceLen = 0
	}
	static termTypes = ["力度记号","力度变化记号","速度记号","变速记号","反复记号"]
	getChoices(items, itemIndex) {
		let item = items[itemIndex]
		this.chinese = item.chinese
		let start = 0 //itemsIndex>5 ? itemsIndex-5 : 0
		let end = items.length //start+10
		if (items.length>10) {
			start = itemIndex>5 ? itemIndex-5 : 0
			if (start+10<end) {
				end = start+10
			}
		}
		for (let i = start; i < end; i++) {
			let item = items[i]
			if (!this.choiceNames.includes(item.chinese)) {
				this.choiceNames.push(item.chinese)
				if (!item.chinese) {
					console.log("error music term")
					console.log(item)
				}
				if (item.chinese.length>this.maxChoiceLen) {
					this.maxChoiceLen = item.chinese.length
				}
			}
		}
		this.choiceIndex = this.choiceNames.indexOf(this.chinese)

		if (item.short) {
			if (item.short instanceof Array) {
				this.english = item.short[0]
			}else{
				this.english = item.short
			}
		}else{
			this.english = item.english
		}
	}
	/*
	config: null为随机和弦
	{
		terms: ["常见术语","其他术语"]
	}
	*/
	static randomMusicTerm(config) {
		let res = new QMusicTerm()
		let type = QNoteElem.randomItem(config.terms)
		let items
		if (type === "其他术语") {
			items = otherTerms
		}else{
			type = QNoteElem.randomItem(this.termTypes)
			items = terms[type]
		}
		let itemIndex = QNoteElem.random(items.length-1)
		res.getChoices(items, itemIndex)

		return res
	}
}

export default QMusicTerm;



