import QNoteElem from './QNote';
import QScore from './QScore';

var rhythmTypes4 = null
var rhythmTypes8 = null
//{"note": {beats: beats, beatUnit: beatUnit, rest: "", notstart: true}}
var rhythmTypeInfo = {
	// 4/4拍： 只在4/4拍出现
	"1": 			{beats: 4, beatUnit: 4},
	// 3/4拍： 只能在3/4和4/4中出现
	"2.": 			{beats: 3, beatUnit: 4},
	"8 4 4 8": 		{beats: 3, beatUnit: 4, notestart: true, rest: "rest8 4 4 8"},
	"16 16 4 4 8":	{beats: 3, beatUnit: 4, notestart: true},
	"8 4 4 16 16": 	{beats: 3, beatUnit: 4, notestart: true, rest: "rest8 4 4 16 16"},
	"16 16 4 4 16 16": {beats: 3, beatUnit: 4, notestart: true},

	// 2/4拍 可以在所有4拍子里出现
	"2": 		{beats: 4, beatUnit: 4},
	"4. 8": 	{beats: 4, beatUnit: 4},
	"4. 16 16": {beats: 4, beatUnit: 4},
	"8 4.": 	{beats: 4, beatUnit: 4, notestart: true, rest: "rest8 4."},
	"16 16 4.": {beats: 4, beatUnit: 4, notestart: true},
	"8 4 8": 	{beats: 4, beatUnit: 4, notestart: true, rest: "rest8 4 8"},
	"16 16 4 8":{beats: 4, beatUnit: 4, notestart: true},
	"8 4 16 16":{beats: 4, beatUnit: 4, notestart: true, rest: "rest8 4 16 16"},
	"16 16 4 16 16": {beats: 4, beatUnit: 4, notestart: true},

	// 1/4拍 可以在所有4拍子里出现
	"4": 	{beats: 1, beatUnit: 4, rest: "rest4"},
	"8 8": 	{beats: 1, beatUnit: 4, rest: "rest8 8"},
	"8. 16": 	{beats: 1, beatUnit: 4},
	"16 8.": 	{beats: 1, beatUnit: 4, notestart: true, rest: "rest16 8."},
	"8 16 16": 	{beats: 1, beatUnit: 4, rest: "rest8 16 16"},
	"16 16 8": 	{beats: 1, beatUnit: 4, rest: "rest16 16 8"},
	"16 8 16": 	{beats: 1, beatUnit: 4, notestart: true, rest: "rest16 8 16"},
	"tuplet3(8 8 8)": 	{beats: 1, beatUnit: 4, rest: "tuplet3(rest8 8 8)"},
	"16 16 16 16": 	{beats: 1, beatUnit: 4, notestart: true, rest: "rest16 16 16 16"},

	// 3/8拍表示只在8拍子里出现的所有节奏形
	"4.": 	{beats: 3, beatUnit: 8},
	"4 8": 	{beats: 3, beatUnit: 8},
	"8 4": 	{beats: 3, beatUnit: 8, notestart: true, rest: "rest8 4"},
	"4 16 16": 	{beats: 3, beatUnit: 8},
	"16 16 4": 	{beats: 3, beatUnit: 8, notestart: true},
	"8 8 8": 	{beats: 3, beatUnit: 8, rest: "rest8 8 8"},
	"16 16 8 8": 	{beats: 3, beatUnit: 8, rest: "rest16 16 8 8"},
	"8 16 16 8": 	{beats: 3, beatUnit: 8, rest: "rest8 16 16 8"},
	"8 8 16 16": 	{beats: 3, beatUnit: 8, rest: "rest8 8 16 16"},

	"16 16 16 16 8": 	{beats: 3, beatUnit: 8, rest: "rest16 16 16 16 8"},
	"16 16 8 16 16": 	{beats: 3, beatUnit: 8, rest: "rest16 16 8 16 16"},
	"8 16 16 16 16": 	{beats: 3, beatUnit: 8, rest: "rest8 16 16 16 16"},
	"16 16 16 16 16 16": 	{beats: 3, beatUnit: 8, rest: "rest16 16 16 16 16 16"},
	"8. 16 8": 		{beats: 3, beatUnit: 8},
	"8. 16 16 16": 	{beats: 3, beatUnit: 8},
	"16 8. 8": 		{beats: 3, beatUnit: 8, notestart: true, rest: "rest16 8. 8"},

	"16 8. 16 16": 	{beats: 3, beatUnit: 8, notestart: true, rest: "rest16 8. 16 16"},
	"8 8. 16": 		{beats: 3, beatUnit: 8, rest: "rest8 8. 16"},
	"16 16 8. 16": 	{beats: 3, beatUnit: 8},
	"8 16 8.": 		{beats: 3, beatUnit: 8},
	"16 16 16 8.": 	{beats: 3, beatUnit: 8},
	"16 8 16 8": 		{beats: 3, beatUnit: 8, notestart: true, rest: "rest16 8 16 8"},
	"16 8 16 16 16": 	{beats: 3, beatUnit: 8, notestart: true, rest: "rest16 8 16 16 16"},
	"8 16 8 16": 		{beats: 3, beatUnit: 8, rest: "rest8 16 8 16"},
	"16 16 16 8 16": 	{beats: 3, beatUnit: 8},
	"tuplet2(8 8)": 	{beats: 3, beatUnit: 8},
	"tuplet4(8 8 8 8)": {beats: 3, beatUnit: 8},
}

class QRhythm extends QScore {
	constructor() {
		super()
		this.notes = []
	}
	/*
	config={
		rests: ["带休止符"]
		beats: ["2/4","3/4","4/4","3/8","6/8"]
		measures: ["2","4","6","8"]
		rhythmTypes: ["1", "2.", ...]
	}
	*/
	static randomRhythm(config) {
		let measures = config.measures ? QNoteElem.randomItem(config.measures) : 2
		let beats = 2
		let beatUnit = 4
		if (config.beats) {
			let strs = QNoteElem.randomItem(config.beats).split("/")
			if (strs.length===2) {
				beats = parseInt(strs[0], 10)
				beatUnit = parseInt(strs[1], 10)
			}
		}
		let types = [] 
		for (let item of config.rhythmTypes) {
			let info = rhythmTypeInfo[item]
			if (beatUnit===8 && info.beatUnit === 8) {
				types.push(item)
			}else if (beatUnit === 4 && info.beatUnit === 4) {
				if (info.beats === 1 || info.beats===2) {
					types.push(item)
				}else if (info.beats === 3) {
					if (beats>=3) {
						types.push(item)
					}
				}else if (info.beats === 4) {
					if (beats>=4) {
						types.push(item)
					}
				}
			}
		}

		let res = new QRhythm()
		res.beats = beats
		res.beatUnit = beatUnit
		for (var i = 0; i < measures; i++) {
			let totalBeats = 0
			while(totalBeats < beats){
				let one = QNoteElem.randomItem(types)
				let info = rhythmTypeInfo[one]
				if (totalBeats+info.beats>beats || (i===0 && info.notestart)) {
					continue
				}
				if (res.notes.length>0 && config.rests.length>0 && info.rest) {
					if (QNoteElem.random(1)) {
						one = info.rest
					}
				}
				totalBeats += info.beats
				res.notes.push(...this.notesOfStaffString(one))
			}
		}

		return res
	}
	static getRhythmTypes4() {
		if (!rhythmTypes4) {
			this.initRhythmTypeInfo()
		}
		return rhythmTypes4
	}
	static getRhythmTypes8() {
		if (!rhythmTypes8) {
			this.initRhythmTypeInfo()
		}
		return rhythmTypes8
	}
	static initRhythmTypeInfo() {
		rhythmTypes4 = []
		rhythmTypes8 = []
		for (let item in rhythmTypeInfo) {
			let info = rhythmTypeInfo[item]
			if (info.beatUnit === 4) {
				rhythmTypes4.push(item)
			}else if (info.beatUnit === 8) {
				rhythmTypes8.push(item)
			}
		}
	}
	/*
	static initRhythmTypeInfo1() {
		rhythmTypeInfo = {}
		for (let key in rhythmTypes) {
			let beats = 2
			let beatUnit = 4
			let strs = key.split("/")
			if (strs.length===2) {
				beats = parseInt(strs[0], 10)
				beatUnit = parseInt(strs[1], 10)
			}

			let items = rhythmTypes[key]
			for (let item of items) {
				let info = {beats: beats, beatUnit: beatUnit}
				if (typeof item === "string") {
					rhythmTypeInfo[item]=info
				}else{
					info.rest = item[1]
					rhythmTypeInfo[item[0]]=info
				}
			}
		}
		rhythmTypes4 = this.loadRhythmTypes([ "4/4", "3/4", "2/4", "1/4"])
		rhythmTypes8 = this.loadRhythmTypes([ "3/8"])
	}
	static loadRhythmTypes(keys) {
		let res = []
		for (let key of keys) {
			let items = rhythmTypes[key].map((item) => {
				if (typeof item === "string") {
					return item
				}else{
					return item[0]
				}
			})
			res.push(...items)
		}
		return res
	}*/
	static notesOfStaffString(staffString) {
		let tuplet
		if (staffString.startsWith("tuplet")) {
			staffString = staffString.substring(6, staffString.length)
			tuplet = parseInt(staffString, 10) 
			staffString = staffString.substring(2, staffString.length-1)
		}

		let strs = staffString.split(" ")
		let notes = []
		// let notes = [
		// {note:8, elems:[{line:0}], beams:[{num: 1, type: "begin"}]},
		// {note:16, elems:[{line:0}], beams:[{num: 1, type: "continue"}, {num: 2, type: "begin"}]},
		// {note:16, elems:[{line:0}], beams:[{num: 1, type: "end"}, {num: 1, type: "end"}]},
		// ]
		for (let str of strs) {
			let noteElem = new QNoteElem.noteFromLine(-1, 0, "treble")
			let note = {note:4, elems:[noteElem]}
			notes.push(note)
			if (str.startsWith("rest")) {
				note.rest = true
				str = str.substring(4, str.length)
			}
			if (str.endsWith(".")) {
				note.dots = 1
				str = str.substring(0, str.length-1)
			}
			note.note = parseInt(str, 10)
		}
		if (tuplet) {
			notes[0].tuplet={count:tuplet}
		}
		
		this.autoBeam(notes)
		return notes
	}
	static autoBeam(notes) {
		for (let i = 0; i < notes.length; i++) {
			let note = notes[i]
			if (note.note<=4) {
				continue
			}
			let prevNote = i>0 ? notes[i-1] : null
			let nextNote = i<notes.length-1 ? notes[i+1] : null

			if ((!prevNote || prevNote.note<=4) && nextNote && nextNote.note>4) {
				note.beams = [{type: 'begin'}]
				if (note.note===16) {
					if (nextNote.note===8) {
						note.beams.push({type: 'forward hook'})
					}else{
						note.beams.push({type: 'begin'})
					}
				}
			}else if (prevNote && prevNote.note>4) {
				let beam
				if (nextNote && nextNote.note>4) {
					beam = 'continue'
				}else{
					beam = 'end'
				}
				note.beams = [{type: beam}]
				if (note.note===16) {
					if (prevNote.note===8) {
						if (nextNote && nextNote.note===16) {
							note.beams.push({type: 'begin'})
						}else{
							note.beams.push({type: 'backward hook'})
						}
					}else if (nextNote && nextNote.note===8) {
						note.beams.push({type: 'end'})
					}else {
						note.beams.push({type: beam})
					}
				}
			}
		}
	}
}

export default QRhythm;



