import React, { Component } from 'react';
import './App.css';
import NavHeader from './pages/Header';
import Home from './pages/Home';

//document.body.style.overflow="scroll";//显示页面水平和垂直滚动条 
// document.body.style.overflow="hidden";//隐藏页面水平和垂直滚动条  

class Storage {
  //refer to https://www.w3.org/TR/IndexedDB/
  constructor() {
    this.dbName = "project"
    this.dbVersion = "1"
    this.db_score_name = "score";
    this.db_config_name = "config";
    this.createdatabase()
  }
  createdatabase() {
    var request = window.indexedDB.open(this.dbName, this.dbVersion);
    let SELF = this
    request.onsuccess = function(event) {        
        SELF.db = request.result;
        // 做其他事情...
    };
    request.onerror = function(event) {   
        console.log("不能打开数据库,错误代码: " + event.target.error);  
    };
    // 下面事情执行于：数据库首次创建版本，或者window.indexedDB.open传递的新版本（版本数值要比现在的高）
    request.onupgradeneeded = function(event) {
      SELF.db = request.result;
      if (event.oldVersion<1) {
        let config_store = SELF.db.createObjectStore(SELF.db_config_name, {keyPath: "key"});
        let score_store = SELF.db.createObjectStore(SELF.db_score_name, {keyPath: "date"});
        let key_index = score_store.createIndex("by_key", "key")
        // let titleIndex = store.createIndex("by_title", "title", {unique: true});
        // var authorIndex = store.createIndex("by_author", "author");
      }

      // Populate with initial data.
      // store.put({title: "Quarry Memories", author: "Fred", isbn: 123456});
      // store.put({title: "Water Buffaloes", author: "Fred", isbn: 234567});
      // store.put({title: "Bedrock Nights", author: "Barney", isbn: 345678});
    };
  }
  put(table, value) {
    // let newItem = { name: "第一个项目", begin: "2017-07-16", end: "2057-07-16", person: "张鑫旭",remark: "测试测试"};

    //此处须显式声明事物  
    var tx = this.db.transaction(table, "readwrite");  
    var store = tx.objectStore(table);  
    var request = store.put(value);
    request.onsuccess = function(){  
        console.log('添加成功');  
    };  
    request.onerror = function(event){  
        console.log(event);
    }
    tx.oncomplete = function() {
      // All requests have succeeded and the transaction has committed.
    };

    tx.onabort = function() {
      // Otherwise the transaction will automatically abort due the failed request.
      //report(tx.error);
    };
  }

  get(table, key, report) {
    //第二个参数可以省略  
      let transaction = this.db.transaction(table,"readonly");  
      let store = transaction.objectStore(table);  
      let request
      if(key)
          request = store.get(key);  
      else  
          request = store.getAll();  

      request.onsuccess = function () {  
          let res = request.result
          console.log(res);
          report(res)
      }
  }
}

class App extends Component {
  constructor(props) {
    super(props)
    window.theApp = this
    this.state = {
      windowHistory: [<Home />]
    }
    this.scores = []
    console.log("start app")
    this.store = new Storage()
    //s.test()
  }
  saveConfig(config) {
    this.store.put("config", config)
  }
  getConfig(key, report) {
    this.store.get("config", key, report)
  }
  saveScore(score) {
    this.store.put("score", score)
  }
  
  pushWindow(component) {
    let windowHistory = this.state.windowHistory
    windowHistory.push(component)
    this.setState({
      windowHistory: windowHistory
    })
  }
  popWindow() {
    let windowHistory = this.state.windowHistory
    if (windowHistory.length>1) {
      let top = windowHistory.splice(windowHistory.length-1, 1)
      top = top[0]
      if (top && top.type.prototype.onClose) {
        top.type.prototype.onClose(top.type)
      }
      this.setState({
        windowHistory: windowHistory
      })
    }
  }
	render() {
		let defaultFontSize = window.device==="piano" ? 28 : 14
    if (window.device === "piano") {
      document.body.style.background = null
      document.body.style.color = 'white'
    }else{
      document.body.style.background = '#666'
      document.body.style.color = 'white'
    }
    let component = this.state.windowHistory[this.state.windowHistory.length-1]
    return (
			<div className="App" style={{fontSize: defaultFontSize}}>
				{window.device !== "piano" ? <NavHeader /> : null}
        {component}
			</div>
		);
	}
}

export default App;



