import midi56 from "../sound/note56-G.mp3";
import midi57 from "../sound/note57-A.mp3";
import midi58 from "../sound/note58-A.mp3";
import midi59 from "../sound/note59-B.mp3";
//小字一组
import midi60 from "../sound/note60-C.mp3";
import midi61 from "../sound/note61-C.mp3";
import midi62 from "../sound/note62-D.mp3";
import midi63 from "../sound/note63-D.mp3";
import midi64 from "../sound/note64-E.mp3";
import midi65 from "../sound/note65-F.mp3";
import midi66 from "../sound/note66-F.mp3";
import midi67 from "../sound/note67-G.mp3";
import midi68 from "../sound/note68-G.mp3";
import midi69 from "../sound/note69-A.mp3";
import midi70 from "../sound/note70-A.mp3";
import midi71 from "../sound/note71-B.mp3";
//小字二组
import midi72 from "../sound/note72-C.mp3";
import midi73 from "../sound/note73-C.mp3";
import midi74 from "../sound/note74-D.mp3";
import midi75 from "../sound/note75-D.mp3";
import midi76 from "../sound/note76-E.mp3";
import midi77 from "../sound/note77-F.mp3";
import midi78 from "../sound/note78-F.mp3";
import midi79 from "../sound/note79-G.mp3";
import midi80 from "../sound/note80-G.mp3";
import midi81 from "../sound/note81-A.mp3";
import midi82 from "../sound/note82-A.mp3";
import midi83 from "../sound/note83-B.mp3";
//小字三组
import midi84 from "../sound/note84-C.mp3";
import midi85 from "../sound/note85-C.mp3";
import midi86 from "../sound/note86-D.mp3";
import midi87 from "../sound/note87-D.mp3";
import midi88 from "../sound/note88-E.mp3";
import midi89 from "../sound/note89-F.mp3";
import midi90 from "../sound/note90-F.mp3";
import midi91 from "../sound/note91-G.mp3";
import midi92 from "../sound/note92-G.mp3";
import midi93 from "../sound/note93-A.mp3";
import midi94 from "../sound/note94-A.mp3";
import midi95 from "../sound/note95-B.mp3";


class MidiPlayer {
	static midiFiles = [
		midi48,midi49,
		midi50,midi51,midi52,midi53,midi54,midi55,midi56,midi57,midi58,midi59,
		midi60,midi61,midi62,midi63,midi64,midi65,midi66,midi67,midi68,midi69,
		midi70,midi71,midi72,midi73,midi74,midi75,midi76,midi77,midi78,midi79,
		midi80,midi81,midi82,midi83,midi84,midi85,midi86,midi87,midi88,midi89,
		midi90,midi91,midi92,midi93,midi94,midi95,
		]
	static playMidiEvents(events) {
		if (window.device==="piano") {
			let notes = events.map((event) => {
				return "0-"+event.note+"-"+event.duration+"-"+event.vel
			})
			console.log(notes)
			document.location="find://playmidi?notes="+notes
		}else {
			this.playMidiMp3(events)
		}
	}
	static playMidiMp3(events) {
		if (!this.audios) {
			this.audios = []
			for (let i = 0; i < 20; i++) {
				this.audios.push(new Audio())
			}
			this.audioIndex = 0
		}
		for (let i = 0; i < events.length; i++) {
			let event = events[i]
			let file = this.midiFiles[events[i].note-48]
			let audioIndex = this.audioIndex%20
			let audio = this.audios[audioIndex]
			this.audioIndex += 1
			if (!audio) {
				console.log("Error, autio null: "+i)
				continue
			}

			if (!audio.ended) {
				audio.pause()
			}
			// audio.pause()
			audio.src=file
			if (event.vel) {
				audio.volume = event.vel/100
			}else{
				audio.volume = 0.7
			}
			console.log("play audio: "+audioIndex+ " currentTime:"+audio.currentTime)
			audio.play()
		}
	}
	/*
	midi = {
		beats: 4
		beatUnit: 4
		events: [{tick:0, note: 69, duration: 480, vel: 70}]
	}
	*/
	static playMidiFile(midi) {
		if (window.device === "piano") {
			let notes = midi.events.map((event) => {
				let res = event.tick+"-"+event.note+"-"+event.duration
				if (event.vel) {
					res += "-"+event.vel
				}
				return res
			})
			console.log(notes)
			let location = "find://playmidi?notes="+notes
			if (midi.countdown) {
				location += "&countdown"
			}
			if (midi.harmony) {
				location += "&harmony"
			}
			if (midi.beats) {
				location += "&beats="+midi.beats
			}
			if (midi.beatUnit) {
				location += "&beatunit="+midi.beatUnit
			}
			document.location=location
			return
		}
		if (midi.harmony) {
			this.playMidiEvents(midi.events)
			// setTimeout(() => {
			// }, 500, this)
			return
		}
		this.midiLoop(midi, (events) => {
			if (events) {
				this.playMidiEvents(events)
			}
		})
	}
	static midiLoop(midi, callback, countdown=true) {
		let startTick = 0
		let midiEvents
		if (countdown) {
			midiEvents = []
			let beatUnit = midi.beatUnit
			let duration
			if (beatUnit===4) {
				countdown = 4
				duration = 480
			}else{
				countdown = 6
				duration = 240
			}
			startTick -= duration*countdown
			for (let i = 0; i < countdown; i++) {
				midiEvents.push({tick: startTick+i*duration, note: 70, duration: duration-10, vel: i===0 ? 80 : 60})
			}
			midiEvents.push(...midi.events)
		}else{
			countdown = 0
			midiEvents = midi.events
		}
		this.midiEventsLoop(midiEvents, (events, playMidiIndex, tick) => {
			if (events && countdown && playMidiIndex<=countdown) {
				this.playMidiMp3(events)
			}else{
				callback(events, playMidiIndex-countdown, tick)
			}
		})
	}
	static midiEventsLoop(midiEvents, callback) {
		if (this.playMidiIndex>=0) {
			return
		}
		this.playMidiIndex = 0
		let msecPerTick = 1000/480.0
		let startTick = midiEvents[0].tick
		let playStartTime = (new Date()).getTime();
		let timeFunc = () => {
			let now = (new Date()).getTime();
			// let duration = now - playStartTime
			let curTick = (now - playStartTime)/msecPerTick+startTick
			//console.log(this.playMidiIndex+":"+duration)
			let events = []
			while(this.playMidiIndex<midiEvents.length) {
				let event = midiEvents[this.playMidiIndex]
				let t = event.tick
				if (t>curTick) {
					break
				}
				events.push(event)
				this.playMidiIndex += 1
			}
			callback(events.length>0 ? events : null, this.playMidiIndex, curTick)
			if (this.playMidiIndex>=midiEvents.length) {
				this.playMidiIndex = -1
			}else{
				window.setTimeout(timeFunc, 10, this)
			}
		}
		timeFunc()
	}
}

export default MidiPlayer;




