
const noteTypes = {1: "whole", 2: "half", 4: "quarter", 8: "eighth", 16: "16th", 32: "32nd"}
class MusicXml {
	constructor() {
		this.divisions = 1024
		this.xmlns = 'http://www.w3.org/2000/svg';
		this.xmlDOM = document.implementation.createDocument(this.xmlns, "xml", null);
	}
	createElem(tag, text, attrs) {
		var elem = this.xmlDOM.createElement(tag)
		if (text) {
			elem.appendChild(this.xmlDOM.createTextNode(text))
		}
		if (attrs) {
			for (var key in attrs){
				elem.setAttribute(key, attrs[key])
			}
		}
		return elem
	}
	pitchOfNote(clef, elem) {
		var pitch = {accidental: elem.accidental}
		if (elem.accidental === "sharp") {
			pitch.alter = 1
		}else if (elem.accidental === "sharp-sharp") {
			pitch.alter = 2
		}else if (elem.accidental === "flat") {
			pitch.alter = -1
		}else if (elem.accidental === "flat-flat") {
			pitch.alter = -2
		}
		var line
		if (clef === "treble") {
			//-6 ... 0 octave:4
			line = elem.line+34
		}else{
			//6 ... 12 octave:4
			line = elem.line+22
		}
		pitch.octave = Math.floor(line/7)
		const steps = ["C", "D", "E", "F", "G", "A", "B"]
		pitch.step = steps[line%7]
		return pitch
	}
	/*
	"(
	((grace,(chord?,(pitch|unpitched|rest)),(tie,tie?)?)|
	(cue,(chord?,(pitch|unpitched|rest)),duration)|
	(chord?,(pitch|unpitched|rest),duration,(tie,tie?)?)),instrument?,
		(footnote?,level?,voice?),type?,dot*,accidental?,
		time-modification?,stem?,notehead?,
		notehead-text?,staff?,beam*,notations*,lyric*,play?
	)"
	*/
	createNoteXml(clef, ticks, note, staff, elemIndex) {
		var noteElem = this.xmlDOM.createElement("note")
		var elem
		if (note.rest) {
			noteElem.appendChild(this.createElem("rest"))
		}else{
			if (elemIndex>0) {
				noteElem.appendChild(this.createElem("chord"))
			}
			elem = note.elems[elemIndex]
			var pitch = this.xmlDOM.createElement("pitch")
			noteElem.appendChild(pitch)

			const val = this.pitchOfNote(clef, elem)
			pitch.appendChild(this.createElem("step", val.step))
			if (val.alter) {
				pitch.appendChild(this.createElem("alter", val.alter))
			}
			pitch.appendChild(this.createElem("octave", val.octave))
		}
		noteElem.appendChild(this.createElem("duration", ticks*this.divisions/480))
		noteElem.appendChild(this.createElem("voice", 1))
		noteElem.appendChild(this.createElem("type", noteTypes[note.note]))
		if (note.dots) {
			for (var i = 0; i < note.dots; i++) {
				noteElem.appendChild(this.createElem("dot"))
			}
		}
		if (elem && elem.accidental) {
			noteElem.appendChild(this.createElem("accidental", elem.accidental))
		}
		noteElem.appendChild(this.createElem("staff", staff))
		if (note.beams) {
			for (var b = 0; b < note.beams.length; b++) {
				const beam = note.beams[b]
				noteElem.appendChild(this.createElem("beam", beam.type, {number:b+1}))
			}
		}
		if ((elem && elem.tieType) || (elemIndex===0 && note.tupletType)) {
			const notations = this.createElem("notations")
			noteElem.appendChild(notations)
			if (elem.tieType) {
				noteElem.appendChild(this.createElem("tie", null, {type:elem.tieType}))
				notations.appendChild(this.createElem("tied", null, {type: elem.tieType}))
			}
			if (note.tupletType) {
				notations.appendChild(this.createElem("tuplet", null, {type: note.tupletType}))
			}
		}
		return noteElem
	}
	createClefElem(clef, staff) {
		var clefElem = this.createElem("clef", null, {number: staff})
		if (clef==="treble") {
			clefElem.appendChild(this.createElem("sign", "G"))
			clefElem.appendChild(this.createElem("line", 2))
		}else{
			clefElem.appendChild(this.createElem("sign", "F"))
			clefElem.appendChild(this.createElem("line", 4))
		}
		return clefElem
	}
	createAttributesElem(part, measureIndex) {
		var attributesElem = this.xmlDOM.createElement("attributes")
		if (measureIndex===0) {
			attributesElem.appendChild(this.createElem("divisions", this.divisions))
		}
		//fifths
		const measure = part.measures[measureIndex]
		if (measure.fifths) {
			var key = this.xmlDOM.createElement("key")
			attributesElem.appendChild(key)
			key.appendChild(this.createElem("fifths", measure.fifths))
			key.appendChild(this.createElem("mode", "major"))
		}
		//time
		if (measure.beats && measure.beatUnit) {
			var time = this.xmlDOM.createElement("time")
			attributesElem.appendChild(time)
			if (measure.beats !== 0) {
				time.appendChild(this.createElem("beats", measure.beats))
				time.appendChild(this.createElem("beat-type", measure.beatUnit))
			}else{
				time.appendChild(this.createElem("senza-misura"))
			}
		}
		if (measureIndex===0) {
			attributesElem.appendChild(this.createElem("staves", part.staves))
		}
	
		//clef
		for (var i = 0; i < part.staves; i++) {
			var clef = null
			if(measureIndex===0){
				clef = part.clefs[i]
			}else if (i<measure.staves.length) {
				var firstNote = measure.staves[i][0]
				if (firstNote && firstNote.type === "clef") {
					clef = firstNote.value
				}
			}
			if (clef) {
				var clefElem = this.createClefElem(clef, i+1)
				attributesElem.appendChild(clefElem)
			}
		}
		if (attributesElem.children.length===0) {
			return null
		}
		return attributesElem
	}
	/*
	<backup>
		<duration>3072</duration>
	</backup>
	*/
	createBackupXml(ticks) {
		var elem = this.xmlDOM.createElement("backup")
		var duration = ticks*this.divisions/480
		elem.appendChild(this.createElem("duration", duration))
		return elem
	}
	createMeasureXml(part, measureIndex, number) {
		var measureElem = this.createElem("measure", null, {number: number})
		// if (measureIndex%3===2) {
		// 	var printElem = this.createElem("print", null, {"new-system": "yes"})
		// 	measureElem.appendChild(printElem)
		// }
		var attributesElem = this.createAttributesElem(part, measureIndex)
		if(attributesElem) {
			measureElem.appendChild(attributesElem)
		}
		
		const measure = part.measures[measureIndex]
		for (var staffIndex = 0; staffIndex < measure.staves.length; staffIndex++) {
			var notes = measure.staves[staffIndex]
			var ticks = 0
			for (var n = 0; n < notes.length; n++) {
				const note = notes[n]
				var noteTicks = measure.noteDuration(note)
				if (note.type === "clef") {
					noteTicks = 0
					attributesElem = this.xmlDOM.createElement("attributes")
					const clefElem = this.createClefElem(note.value, staffIndex+1)
					attributesElem.appendChild(clefElem)
					measureElem.appendChild(attributesElem)
				}else if (note.rest) {
					const noteElem = this.createNoteXml(part.clefs[staffIndex], noteTicks, note, staffIndex+1)
					measureElem.appendChild(noteElem)
				}else if (note.elems) {
					for (var e = 0; e < note.elems.length; e++) {
						const noteElem = this.createNoteXml(part.clefs[staffIndex], noteTicks, note, staffIndex+1, e)
						measureElem.appendChild(noteElem)
					}
				}
				ticks += noteTicks
			}
			if (staffIndex < measure.staves.length-1) {
				const backupElem = this.createBackupXml(ticks)
				measureElem.appendChild(backupElem)
			}
		}
		return measureElem
	}
	createXML(score) {
		this.score = score
		var s = this.createElem("score-partwise", null, {version: "3.0"})
		//work
		if (score.title) {
			var work = this.xmlDOM.createElement("work")
			s.appendChild(work);
			var workTitle = this.xmlDOM.createElement("movement-title")
			work.appendChild(workTitle)
			workTitle.appendChild(this.xmlDOM.createTextNode(score.title))
		}
		//part-list
		var partList = this.xmlDOM.createElement("part-list")
		s.appendChild(partList)
		for (var i = 0; i < score.parts.length; i++) {
			let part = score.parts[i]
			let id = "P"+i
			let scorePart = this.createElem("score-part", null, {id:id})
			partList.appendChild(scorePart)
			scorePart.appendChild(this.createElem("part-name",part.name))
		}
		//part
		for (var j = 0; j < score.parts.length; j++) {
			let part = score.parts[j]
			let partElem = this.createElem("part", null, {id: "P"+j})
			s.appendChild(partElem)
			//measures
			for (var m = 0; m < part.measures.length; m++) {
				const measureElem = this.createMeasureXml(part, m, m+1)
				partElem.appendChild(measureElem)
			}
		}
		return s;
	}
}

const xmlHeader = "<?xml version=\"1.0\" encoding='UTF-8' standalone='no' ?>\n<!DOCTYPE score-partwise PUBLIC \"-//Recordare//DTD MusicXML 3.0 Partwise//EN\" \"http://www.musicxml.org/dtds/partwise.dtd\">\n"
export function saveScore(score) {
	var xml = new MusicXml()
	var obj = xml.createXML(score)
	const doc = new XMLSerializer().serializeToString(obj) //createXML("test")
	const str = xmlHeader+doc
	// console.log(str);

	// return loadScore(str);
	return str
}
const typeNotes = {"whole": 1, "half": 2, "quarter": 4, "eighth": 8, "16th": 16, "32nd": 32}

class MusicXmlParser {
	constructor() {
		this.divisions = 1024
		this.xmlns = 'http://www.w3.org/2000/svg';
		this.xmlDOM = document.implementation.createDocument(this.xmlns, "xml", null);
		this.curClefs = ["treble", "treble"]
	}
	enumateChildren(elem, func) {
		var children = elem.childNodes
		for (var i = 0; i < children.length; i++) {
			var xmlElem = children[i]
			func(this, xmlElem)
		}
	}
	parseClef(clefElem){
		var sign = this.getChildText(clefElem, "sign")
		var line = this.getChildText(clefElem, "line")
		var number=clefElem.getAttribute("number")
		// console.log(number)
		if (number) {
			number = parseInt(number, 10)-1
		}
		// console.log(number)

		var type = "treble"
		if (sign==="G" && line==="2") {
			type="treble"
		}else if (sign==="F" && line==="4") {
			type="bass"
		}
		return {number: number, type: type}
	}
	getChildText(elem, childTagName) {
		var children = elem.getElementsByTagName(childTagName)
		if (children && children.length>0) {
			return children[0].textContent
		}
		return null
	}
	getChildInt(elem, childTagName) {
		var text = this.getChildText(elem, childTagName)
		if (text) {
			return parseInt(text, 10)
		}
		return null
	}
	parseAttribute(measureJson, attrElem) {
		this.enumateChildren(attrElem, function(self, elem){
			if (elem.tagName==="divisions") {
				self.divisions = elem.textContent
			}else if (elem.tagName==="staves") {
				measureJson.stavesNumber = parseInt(elem.textContent, 10)
			}else if (elem.tagName==="key") {
				measureJson.fifths = self.getChildInt(elem, "fifths")
			}else if (elem.tagName==="time") {
				measureJson.beats = self.getChildInt(elem, "beats")
				measureJson.beatUnit = self.getChildInt(elem, "beat-type")
			}else if (elem.tagName==="clef") {
				var clef = self.parseClef(elem)

				if (!measureJson.clefs) {
					measureJson.clefs = []
				}
				if (clef.number<measureJson.clefs.length) {
					measureJson.clefs[clef.number] = clef.type
				}else{
					measureJson.clefs.push(clef.type)
				}
				self.curClefs[clef.number] = clef.type
			}
		})
	}
	lineOfPitch(clef, step, octave) {
		const steps = ["C", "D", "E", "F", "G", "A", "B"]

		var line = steps.indexOf(step)
		if (clef === "treble") {
			line -= 6
			line += (octave-4)*7
		}else{
			line -= 1
			line += (octave-3)*7
		}
		return line
	}
	parsePitch(elem) {
		var step = this.getChildText(elem, "step")
		var octave = this.getChildInt(elem, "octave")
		return {step: step, octave: octave}
	}
	parseNote(measureJson, noteXmlElem) {
		var noteType = 0
		var dots = 0
		var staff = 0
		var isChord = false
		var isRest = false
		var pitch = null
		var noteElem = {line: 0}
		var beams = null
		this.enumateChildren(noteXmlElem, function(self, xmlElem){
			switch(xmlElem.tagName) {
			case "rest":
				isRest = true
				if (xmlElem.getAttribute("measure") === "yes") {
					noteType = 1
				}
				break
			case "chord":
				isChord = true
				break
			case "pitch":
				pitch = self.parsePitch(xmlElem)
				break
			case "duration":
				break
			case "type":
				noteType = typeNotes[xmlElem.textContent]
				break
			case "staff":
				staff = parseInt(xmlElem.textContent, 10)-1
				break
			case "dot":
				dots += 1
				break
			case "accidental":
				noteElem.accidental=xmlElem.textContent
				break
			case "beam":
				var num = parseInt(xmlElem.getAttribute("number"),10)-1
				var type = xmlElem.textContent
				if (beams==null) {
					beams = []
				}
				beams.push({num: num, type: type})
				break
			default:
				break
			}
		})
		if (pitch) {
			noteElem.line = this.lineOfPitch(this.curClefs[staff], pitch.step, pitch.octave)
		}

		if (!isChord) {
			var note = {note: noteType, elems:[noteElem]}
			if (dots) {
				note.dots = dots
			}
			if (beams) {
				note.beams = beams
			}
			if (isRest) {
				note.rest = true
			}
			if (staff<measureJson.staves.length) {
				measureJson.staves[staff].push(note)
			}else{
				measureJson.staves.push([note])
			}
		}else{
			var notes = measureJson.staves[staff]
			if (!notes) {
				notes = measureJson.staves[(staff>0 ? 0 : 1)]
				console.log(measureJson.staves)
				console.log(noteXmlElem)
			}
			notes[notes.length-1].elems.push(noteElem)
		}
	}

	parseMeasure(partJson, measureElem) {
		var measureJson = {staves: []}
		this.enumateChildren(measureElem, function(self, xmlElem){
			if (xmlElem.tagName === "attributes") {
				self.parseAttribute(measureJson, xmlElem)
				if (measureJson.stavesNumber) {
					partJson.staves = measureJson.stavesNumber
				}
			}else if (xmlElem.tagName === "note") {
				self.parseNote(measureJson, xmlElem)
			}
		})
		partJson.measures.push(measureJson)
	}

	parsePart(partJson, partElem) {
		var measures = partElem.getElementsByTagName("measure")
		for (var m = 0; m < measures.length; m++) {
			var measureElem = measures[m]
			this.parseMeasure(partJson, measureElem)
		}
	}
	parseXml(xmlDoc) {
		var json = {}
		json.title = this.getChildText(xmlDoc, 'movement-title')
	    var scoreParts = xmlDoc.getElementsByTagName('score-part')
	    json.parts=[]
	    for (var i = 0; i < scoreParts.length; i++) {
	    	var p = scoreParts[i]
	    	var partName = this.getChildText(p, "part-name")
	    	if (partName === "MusicXML Part") {
	    		partName = ""
	    	}
	    	var partJson = {id: p.id, name: partName, staves: 1, measures: []}
	    	var partElems = xmlDoc.getElementsByTagName("part")
	    	for (var partIndex = 0; partIndex < partElems.length; partIndex++) {
				var partElem = partElems[partIndex]
				if (partElem.id === p.id) {
		    		this.parsePart(partJson, partElem)
				}
	    	}
	    	json.parts.push(partJson)
	    }
	   	console.log(json)
	   	return json
	}
}

export function loadScore(xmlString) {
	//创建文档对象
	var parser=new DOMParser();
	var xmlDoc=parser.parseFromString(xmlString,"text/xml");
	
	var xml = new MusicXmlParser()
	return xml.parseXml(xmlDoc)
}



